import * as xlsx from 'xlsx';
import { LocalStorageService } from './../core/service/local-storage.service';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { UserSession } from 'core/sessions';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { HttpSuccesFailureResponse } from '../core/http_wrapper/http_wrapper_response';
import { Constants } from '../core/constants';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent } from 'classes/confirmationPopup/confirmationPopup.component';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { BaseComponent } from 'app/base/base.component';

declare var $: any;



@Component({
  selector: 'app-package-list',
  templateUrl: './package-list.component.html',
  styleUrls: ['./package-list.component.css']
})
export class PackageListComponent extends BaseComponent implements OnInit,HttpSuccesFailureResponse {

  @ViewChild('bkgtable') bkgtable: ElementRef;

  startDate: any="2017/07/10";
  endDate:any="2017/10/10";
  headerTitleGYMRegular=[{name:"Sr No",view:false,id:0},{name:"Title",view:false,id:0},{name:"Description",view:false,id:0},{name:"Validity",view:false,id:0},{name:"Price",view:false,id:0},{name:"Sessions",view:false,id:0},{name:"Age Group",view:false,id:0},{name:"Starting Date",view:false,id:0},{name:"Booking preference",view:false,id:0},{name:"Status",view:false,id:0},{name:"Action",view:false,id:0}]
  userType;
  listIsLaoding;
  packageData:any=[];
  packageDataFromApi:any=[];
searchText="";
packageToDelete;
gymList=[];
  isMasterGym:boolean=false;
  gymId=-1;


  ngAfterViewInit() {

    var that=this;
    $(function() {
      $('input[name="daterange"]').daterangepicker({ 
        locale: {
            format: 'YYYY/MM/DD'
        },
        startDate: that.startDate,
        endDate:  that.endDate,
        // maxDate:new Date(),
    });
  });
 

  $('input[name="daterange"]').on('apply.daterangepicker', function(ev, picker) {
that.dateRangeChange(picker.startDate._d,picker.endDate._d);
});

    $(document).ready(function() {
        window.alert = function(){};
    });

  }


  constructor(private route: ActivatedRoute,private router: Router,private modal: Modal,private http_userauth: HttpUserAuthUtilsService) {
    super(http_userauth);
   }
  
  ngOnInit(): void {
    this.loadAdminDataIfLoggenIn();
    this.userType = LocalStorageService.getUserType();
    this.isMasterGym = LocalStorageService.isMasterGym();

    if(this.isMasterGym){
      this._userAuthHttpService.getGymList(this,true);
    }
    else{
      this.gymId=LocalStorageService.getUserId();

    }
    this.route.params.subscribe(params => {
      
      
      });
      this.getPackageList();

  }


  dateRangeChange(start,end){
    this.startDate=UserSession.getStartDate(start);
    this.endDate=UserSession.getStartDate(end);
    if(LocalStorageService.getUserType()==2){
      this.router.navigate(['trainer/bookings',this.startDate,this.endDate]);
    }else{
      this.router.navigate(['gym/bookings',this.startDate,this.endDate]);
  
  }
  this.searchText ="";
  return;
  }


 
  getPackageList() {
       
          this._userAuthHttpService.getAllMembershipPackages(this,true,this.gymId);

      
    }

    onSuccess(type: any, responsedata: any) {
      if(type==Constants.getPackageListApi){
        this.packageData=[]
        responsedata.data.forEach(element => {
          if(element.packageType=="GENERAL"||element.packageType=="ARIALPACK"||element.packageType=="DROPIN"||element.packageType=="SPECIALCLASSCATEGORY"||element.packageType=="GYMSPECIFIC" || (element.packageType=="AUTOJOINWITHPACKAGE"&&element.classId)){
            if(element.noOfClass==Constants.unlimitedClasses){
              element.noOfClass = "UNLIMITED CLASSES"
            }
            this.packageData.push(element);
          }
          
        });
       
      }
      if(type==Constants.editExistingPackageApi){
         this.getPackageList();
      }
      if(type == Constants.gymListApi){
        this.gymList = responsedata.data;
        this.gymList = this.gymList.filter((element) => {
          return element.id != LocalStorageService.getMasterGymId();
       });
      }
     
    }
    onFailure(type: any, response: any) {
      if(type == Constants.getPackageListApi){
       
      }
      if(type==Constants.editExistingPackageApi){
        
      }
    }

    deletePackage(item){
      
      this.packageToDelete = item;
     this.openconfirmpopup("Do you really want to remove "+item.name+" ?","Are you Sure?","Yes","No",this,"PACKAGE_DELETE")
    }
    
    openconfirmpopup(message, title,yesTitle,noTitle, parent?: any, errorCode?: any) {
      return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title ,yesTitle,noTitle, parent, errorCode}, BSModalContext));
  
    }
    confirmAction(){
      let request={
        "name":this.packageToDelete.name,
      "arName":this.packageToDelete.name,
      "imageSrc":"",
      "description":this.packageToDelete.description,
      "cost":this.packageToDelete.cost,
      "packageType":this.packageToDelete.packageType,
      "duration":this.packageToDelete.duration,
      "validityPeriod":this.packageToDelete.validityPeriod,
      "noOfClass":this.packageToDelete.noOfClass=="UNLIMITED CLASSES"?Constants.unlimitedClasses:this.packageToDelete.noOfClass,
      "status":false,
      "isDeleted":true
    }


      
    this._userAuthHttpService.editPackage(this.packageToDelete.id,request,this,true);

    }
    public gerStatus(item:any){
     if(item.status.toString()=='true'){
       return "Active"
     }else{
      return "Inactive"

     }
    }

    cancelAction(){
      }
    addBooking(){
      this.router.navigate(['addBookingModule']);
    }
   
  editPackage(item){
    this.router.navigateByUrl("addEditPackage/"+item.id)
  }
  addPackage(){
    this.router.navigateByUrl("addEditPackage");

  }
  selectGym(event){
  
    let seletedGym=event.target.value;
    this.gymId=seletedGym;
    this.getPackageList();

  }

  public onFilterApply(event){}

  getStartingDate(packageStartDate:any){
   if(packageStartDate&&packageStartDate!='null'){
if(UserSession.checkisPastDate(packageStartDate)){
  return "Purchase Date";

}else{
      return packageStartDate;
}
   }else{
    return "Purchase Date";
   }
  }
 

    downloadExcel() {
      const ws: xlsx.WorkSheet =   
      xlsx.utils.table_to_sheet(this.bkgtable.nativeElement);
      const wb: xlsx.WorkBook = xlsx.utils.book_new();
      xlsx.utils.book_append_sheet(wb, ws, 'Sheet1');
      xlsx.writeFile(wb, 'trainersExcel.xlsx');
     }

}


